.page-design,
.text-wrapper-part {
  /*padding-top: 60px;*/
  backface-visibility: hidden;
}
section.no-animated,
.page-design .main-footer {
  position: relative;
}
.contact-form-wrapper {
  background: #fff;
}
.behance-link-section h2.section-heading {
  padding-left: 50px;
  padding-right: 50px;
}
.design-top-header {
  padding-top: 0;
}
.parallax-heading,
.animated-part {
  position: relative;
  width: auto;
  overflow: hidden;
}
.parallax-heading {
  background: #fff;
  margin-bottom: 25vh;
}
.parallax-heading {
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
  position: relative;
}
.parallax-heading .background,
.parallax-heading .midground,
.parallax-heading .foreground {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  transition: transform 0.05s linear;
}
.parallax-heading .top-layer {
  font-size: 72px;
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 12.5em;
  margin-left: -6.25em;
  text-align: center;
  margin-top: -2.8em;
  padding: 1.7em 0 ;
  line-height: 1.1em;
  box-sizing: border-box;
  transform: translate3D(0, 0, 0);
  transition: transform 0.1s linear 0.03s;
}
.top-layer .design-top-header {
  position: absolute;
  width: 100%;
  top: 0;
  box-sizing: border-box;
}
.design-slogan {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  line-height: 1.1em;
  color: #494949;
  text-align: center;
}
//headers showing depends of device - start
.page-design .design-desktop-only {
  display: block;
}
.page-design .design-mobile-only {
  display: none;
}
.design-mobile .design-desktop-only {
  display: none;
}
.design-mobile .design-mobile-only {
  display: block;
}
//end

.blue-color {
  color: #57a0b8;
}
.bg-item,
.mg-item {
  position: absolute;
}
.animated-panel-wrapper {
  display: block;
  overflow: hidden;
  position: relative;
  height: 100vh;
}
.panel {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
}
.panel .sliding-part {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.panel .img-part .sliding-part {
  background: rgba(255, 255, 255, 1);
  left: 0;
}
.panel .text-part .sliding-part {
  background: rgba(255, 255, 255, 1);
  right: 0;
}
