/*-----------------------scroll buttons-----------------------*/
.scroll-bottom {
  display: block;
  height: 60px;
  width: 20px;
  opacity: .3;
  margin: 48px auto 20px;
}
.no-touch .scroll-bottom:hover {
  opacity: .8;
}
.transparent-panel .sliding-part .img-fg {
  background-color: #fff;
}
.transparent-panel.img-part .sliding-part {
  background: transparent;
}
