  position: absolute;
  left: 50%;
  margin: 0 0 0 -10px;
  top: 100%;
}
.scroll-bottom svg {
  height: 60px;
  width: 12px;
}
/*-----------animated parts with heading and images-----------*/

.animated-part__wrapper {
  overflow:hidden;
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: row;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  width: 100%;
  margin: auto;
  height: 100vh;
}
.flex-reverse {
  -webkit-flex-direction: row-reverse;
  -ms-flex-direction: row-reverse;
  flex-direction: row-reverse;
}
.animation1 {
  padding-bottom: 5vh;
}
.img-part,
.text-wrapper-part {
  width: 50%;
  height: 100%;
  position: relative;
  text-align: center;
  overflow: hidden;
}
.text-part {
  width: 100%;
  height: 100vh;
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: column;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.img-part img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
}
.animated-part-description,
.animated-part-slogan {
  text-align: center;
  font-family: "Roboto", sans-serif;
  width: auto;
  max-width: 480px;
  padding: 0 30px;
}
.animated-part-description {
  color: #777676;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  margin: 0 auto;
}
.mobile-description {
  display: none;
}
.animated-part-slogan {
  font-size: 60px;
  font-weight: 300;
  line-height: 1.4em;
  color: #57a0b8;
  margin: 15px auto;
  overflow: hidden;
}
.animated-part-slogan.small-font {
  font-size: 40px;
  font-weight: 700;
  margin: 0 auto 38px;
}
.animated-part-slogan .black-accent {
  font-weight: 500;
  color: #333;
}
.explore-btn {
  display: block;
  margin: 48px auto 0;
  width: 172px;
  text-align: center;
  padding: 22px 0;
  background-color: #0074ff;
  border: 2px solid #0074ff;
  border-radius: 34px;
  /*box-shadow: 0 4px 9px rgba(0, 116, 255, 0.2);*/
  color: #fff;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
}
.no-touch .explore-btn:hover {
  box-shadow: inset 0 0 0 2px #fff;
}
/*------sections after animation-----*/
.behance-link-section {
  background-color: #f8f8f8;
  padding-bottom: 90px;
  overflow: hidden;
}
.social-design-links {
  margin: 0;
  text-align: center;
}
.social-design-links li {
  display: inline-block;
  margin: 0 13px;
}
.design-social-link {
  display: block;
  margin: auto;
  border: 2px solid transparent;
  width: 106px;
  height: 106px;
  border-radius: 50%;
  font-size: 16px;
  text-align: center;
  line-height: 102px;
  vertical-align: middle;
  text-decoration: none;
  text-transform: uppercase;
  transition: all .06s;
  -webkit-transition: all .06s;
  box-sizing: border-box;
  position: relative;
}
.design-social-link.behance {
  background: #2d51eb;
}
.design-social-link.dribbble {
  background: #ea4c89;
}
.no-touch a.design-social-link:hover {
  box-shadow: inset 0 0 0 2px #fff;
  border-color: transparent;
}
.design-social-icon {
  display: inline-block;
  line-height: inherit;
  vertical-align: inherit;
  height: 45px;
  width: 45px;
  fill: #fff;
}
.scrolled-top {
  margin-top: -100%;
}
