/*oly for mobile styles*/
.design-mobile .sliding-part,
.design-mobile .hide-for-mobile-design {
  display: none !important;
}
.design-mobile .mobile-description {
  display: block;
}
.design-mobile .animated-part-slogan.small-font {
    font-size: 30px;
}
.design-mobile .parallax-heading {
  margin-bottom: 10vh;
}
.design-mobile .transparent-panel .full-color {
  opacity: 1;
}
.transparent-panel.img-part .sliding-part {
  background: transparent;
}

@media all and (orientation: landscape) {
  .design-mobile .img-part,
  .design-mobile .text-wrapper-part {
    width: 100%;
    height: 100vh;
    display: flex;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    flex-direction: column;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .design-mobile .img-part img {
    position: static;
    margin: auto !important;
  }
}

@media all and (max-width: 767px) and (orientation: landscape) {
  .animated-panel-wrapper,
  .animated-part__wrapper {
    height: auto;
    min-height: 320px;
  }
  .design-mobile .img-part,
  .design-mobile .text-wrapper-part,
  .design-mobile .text-wrapper-part .text-part {
    height: auto;
    min-height: 320px;
  }
  .animated-part-description {
    font-size: 14px;
  }
  .design-mobile .animated-part-slogan.small-font {
    font-size: 24px;
  }
  .explore-btn {
    padding: 14px 0;
    border-radius: 26px;
  }
}
