  .design-mobile .img-part,
  .design-mobile .text-wrapper-part,
  .design-mobile .text-wrapper-part .text-part {
    display: block;
    height: auto;
  }
  .design-mobile .text-wrapper-part {
    padding: 10px 0 30px;
  }
}

