.flexible-section .flexible-list {
  width: auto;
  max-width: 980px;
  margin: 0 auto;
  padding: 0;
  font-size: 0;
  text-align: center;
  vertical-align: top;
}
.flexible-section .flexible-list li {
  display: inline-block;
  width: auto;
  vertical-align: top;
  box-sizing: border-box;
}
.flexible-section .flexible-list li p {
  font-size: 18px;
}

/*                   */
/*   block abilities */
/*                   */

.flexible-section.abilities {
  margin: 0 auto;
  padding: 6px 0 30px;
}
.flexible-section .abilities-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-justify-content: center;
  max-width: 986px;
  margin: auto;
  padding: 0;
  overflow: hidden;
}
.flexible-section .abilities-list li {
  background: #fff;
  margin: 0 3px 5px;
  padding: 0;
  width: calc( 25% - 6px);
  text-align: left;
}
.flexible-section .abilities-list li svg {
  fill: #fff;
}
.flexible-section .abilities-list li p {
  font-size: 16px;
  line-height: 1.5;
}
.flexible-section .ability .item-container {
  display: block;
  width: 100%;
  height: auto;
  padding-top: 25px;
  min-height: 31px;
  margin: 0;
  position: relative;
  overflow: hidden;
}
.flexible-section .healthcare-item .item-container {
  display: block;
  width: 100%;
  height: auto;
  padding: 20px 30px 0;
  margin: 0;
  overflow: hidden;
}
.healthcare-item .item-container .icon-holder {
  height: 24px;
  width: 24px;
  margin: 0;
  display: block;
  vertical-align: middle;
  line-height: 24px;
  text-align: left;
}
.healthcare-item .item-container .icon-holder img {
  vertical-align: middle;
}
.ability .item-container .icon-holder {
  height: 56px;
  width: 56px;
  margin: 0 auto;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -28px;
  margin-top: -28px;
  vertical-align: bottom;
  line-height: 56px;
  text-align: center;
}
.ability .icon-bg {
  height: auto;
  width: auto;
  margin: -25px auto 0;
  max-height: 168px;
  min-width: 100%;
}
.flexible-section .ability-text{
  padding: 20px 30px 25px;
  margin: 0;
}
.flexible-section .icon-holder img {
  height: 100%;
  width: auto;
  max-height: 100%;
  display: inline-block;
  margin: 0 auto;
  line-height: inherit;
  vertical-align: bottom;
}
.flexible-section.healthcare .flexible-caption {
  font-size: 26px;
  font-weight: 400;
  margin: 0 auto 34px;
  width: auto;
}

