/*                      */
/* blog section         */
/*                      */

.selected-block + .flexible-section.chatbot-blog {
  margin-bottom: 35px;
}
.selected-block + .flexible-section.chatbot-blog .blog-list-caption {
  padding-top: 0;
}

.flexible-section.chatbot-blog {
  padding: 10px 0 52px;
  background-color: #fff;
}
.flexible-section .blog-list-caption {
  position: relative;
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 54px 0;
  letter-spacing: 0.12em;
}

.flexible-section .flexible-blog-list {
  max-width: 980px;
}

.flexible-section .flexible-blog-list li {
  width: 33.33%;
  padding: 0 30px;
  text-align: left;
}

.flexible-section .new-style-header {
  color: black;
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
  font-weight: 500;
  text-transform: uppercase;
}

.flexible-section .flexible-blogpost-title {
  color: #303030;
  font-size: 22px;
  line-height: 1.27;
  font-weight: 600;
  margin: 0 0 18px;
  text-align: left;
  transition: color .06s ease;
  -webkit-transition: color .06s ease;
}
.flexible-section .flexible-blogpost-link {
  display: block;
  text-decoration: none;
  border-bottom-color: transparent !important;
}
.no-touch .flexible-blogpost-link:hover .flexible-blogpost-title {
  color: #e95937;
}
.flexible-section .flexible-blog-list .blogpost-short-content {
  color: #656565;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
}

.flexible-section .blogpost-short-content:after {
  content: '...';
}
.flexible-section.chatbot-blog + .selected-block:before {
  background: transparent;
}
