  letter-spacing: normal;
}
/*unordered lists*/

.service-text-part ul,
.service-text-part ol {
  margin:0 auto 30px;
  padding:0 0 0 57px;
  box-sizing: border-box;
  width: auto;
  max-width: 840px;
}
.service-text-part ul li,
.service-text-part ol li {
  padding: 0 90px 0 17px;
  list-style: none;
  position: relative;
  line-height: 1.5;
  margin: 20px 0;
  text-align: left;
}
.service-text-part ul li:before,
.service-text-part ol li:before {
  display: block;
  position: absolute;
  left: -18px;
}
.service-text-part ul li:before {
  content: '';
  top: .5em;
  width: 8px;
  border-top: 8px solid #f1563c;
}
/*new service page sections*/
.service-description {
  background-color: #f3f3f3;
  padding: 0;
  margin: 0;
}
.service-description-container {
  width: auto;
  max-width: 840px;
  margin: 0 auto;
  padding: 0 70px;
  text-align: justify;
}
/*custom styled sections*/
.green-bg {
  background-color: #a4c439;
  color: #212121;
}
.black-bg {
  background-color: #212121;
  color: #a3a3a3;
}
.black-bg .secondary-heading {
  color: #fff;
}
.page-mobile-service .page-services .black-bg li,
.page-mobile-service .page-services .black-bg p {
  color: #a3a3a3;
}
