  color: #212121;
}
.green-bg p,
.green-bg li {
  color: #212121;
}
.green-bg .service-text-part ul li:before {
  border-color: #fff;
}
/*headings*/
.secondary-heading {
  margin: 3px auto 24px;
  font-size: 32px;
  color: #212121;
  font-weight: 400;
  line-height: 1;
}
.big-heading {
  font-size: 54px;
  font-weight: 300;
}
.small-heading {
  font-family: 'OpenSans', sans-serif;
  font-size: 24px;
  font-weight: 600;
}
.heading-icon {
  height: 42px;
  width: 37px;
  display: inline-block;
  line-height: inherit;
  font-size: inherit;
  margin-right: .25em;
}

/*sections*/
.service-flexible-content {
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: row;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
}
.service-image-part img {
  display: block;
  margin-left: -150px;
}
/**/
.service-columns-content {
  margin-top: -1px;
  overflow: hidden;
}
.service-columns-content:before {
  content: '';
  display: block;
  width: 100%;
  max-width: 840px;
  border-top: 1px solid #ccc;
}
.flex-reverse {
  flex-direction: row-reverse;
  -webkit-flex-direction: row-reverse;
  -ms-flex-direction: row-reverse;
}
.service-image-part,
.service-text-part {
  height: 100%;
  display: block;
  box-sizing: border-box;
  min-width: 50%;
}
.service-text-part {
  text-align: justify;
  max-width: 400px;
  padding: 48px 0 48px 10px;
  overflow: hidden;
  margin: auto;
}
.reverse .service-text-part {
  padding: 48px 10px 48px 0;
}
.service-custom-block .service-text-part {
  padding: 45px 0 18px;
}
.thin-column {
  width: auto;
  max-width: 650px;
}
.thin-column .secondary-heading {
  text-align: center;  
}
.wide-column.service-text-part,
.wide-column {
  width: auto;
  margin: auto;
  padding-left: 0;
  max-width: 840px;
}
.service-image-part {
  padding: 0 30px;
  float: left;
}
.reverse .service-image-part {
  float: right;
  padding: 0 30px;
}
.service-image-part img {
  display: block;
  height: 100%;
  width: auto;
  margin: 0 auto;
  float: left;
}
.reverse .service-image-part img {
  margin: 0;
  float: right;
}
.service-image-part + .service-text-part {
  padding-bottom:0;
}
.service-image-part.left-side-image {
  padding-top: 60px;
  /*padding-bottom: 50px;*/
  align-self: center;
}
.service-image-part.left-side-image img {
  margin-left: -180px;
}
.will-stagger1,
.will-stagger2 {
  -webkit-transition: opacity 1000ms cubic-bezier(0.25, 0.1, 0.25, 1),-webkit-transform 1000ms cubic-bezier(0.25, 0.1, 0.25, 1);
  transition: opacity 1000ms cubic-bezier(0.25, 0.1, 0.25, 1),transform 1000ms cubic-bezier(0.25, 0.1, 0.25, 1);
  opacity: 0.01;
  -webkit-transform: translate(0, 100px);
  -ms-transform: translate(0, 100px);
  transform: translate(0, 100px);
}
.staggered.will-stagger1,
.staggered.will-stagger2 {
  opacity: 1;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
}
